import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";
import i1 from "./assets/images/image1.jpeg";
import i2 from "./assets/images/image2.jpeg";
import i3 from "./assets/images/image3.jpeg";
import gif from "./assets/images/gif2.gif";
import etherscan from "./assets/images/feefer.png";
import discord from "./assets/images/peeper.png";
import twitter from "./assets/images/tweeter.png";
import opensea from "./assets/images/opper.png";
import styles from "./styles/index.css";

export const StyledButton = styled.button`
  border-radius: 50px;
  border: solid;
  background-color: #ff8932;
  padding: 0px;
  text-align: center;
  text-shadow: 2px 2px 2px #000000;  
  padding-left: 40px;
  box-shadow:
    0 0 15px 7px #fff,  /* inner white */
    0 0 25px 15px #FFE433, /* middle magenta */
    0 0 35px 35px #FF2300; /* outer cyan */

  font-weight: bold;
  font-size: 40px;
  color: white;
  cursor: pointer;
  // box-shadow: 0px 6px 0px -2px black;
  // -webkit-box-shadow: 0px 6px 0px -2px black;
  // -moz-box-shadow: 0px 6px 0px -2px black;
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    color: #white;
  }
  :hover {
    transition-duration: .4s;

  }
  @media (min-width: 767px) {
    padding-left: 40px;
    text-shadow: 2px 2px 2px #000000;  
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledImg = styled.img`
  width: 403px;
  height: 176.5px;
  box-shadow:
  0 0 60px 30px #fff,  /* inner white */
  0 0 100px 60px #f0f, /* middle magenta */
  0 0 140px 90px #0ff; /* outer cyan */
  @media (min-width: 767px) {
    width: 806px;
    height: 337px;
    box-shadow:
    0 0 60px 30px #fff,  /* inner white */
    0 0 100px 60px #f0f, /* middle magenta */
    0 0 140px 90px #0ff; /* outer cyan */
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [feedback, setFeedback] = useState("");
  const [claimingNft, setClaimingNft] = useState(false);
  const claimNFTs = (_amount) => {
    _amount = document.getElementById("inputBox").value;
    if (_amount <= 0) {
      return;
    }
    setFeedback("Minting your Official DragonX NFTs...");
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(blockchain.account, _amount)
      // ********
      // You can change the line above to
      // .whiteListMint(blockchain.account, _amount) if you want only whitelisted
      // users to be able to mint through your website!
      // And after you're done with whitelisted users buying from your website,
      // You can switch it back to .mint(blockchain.account, _amount).
      // ********
      .send({
        gasLimit: 285000 * _amount,
        to: "0xcC9399412501a3C0a8DB8CbF6639f15f96b6db86",
        from: blockchain.account,
        value: blockchain.web3.utils.toWei((0.055 * _amount).toString(), "ether"),
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong. Check your transaction on Etherscan to find out what happened!");
        setClaimingNft(false);
      })
      .then((receipt) => {
        setFeedback(
          "CONGRATS! Your DragonX NFTs successfully minted!"
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen style={{ backgroundColor: "var(--black)", fontSize: 40 }}>
      <s.Container flex={1} ai={"center"} style={{ padding: 0, backgroundImage: "linear-gradient(to bottom, red , yellow)", flexDirection: "column", }}>
        <div className="header" style={{ marginLeft: 250, marginRight: 250, marginTop: 100, marginBottom: 0 }}>
          <div className="header-text">
            {/* <h1 style={{fontSize: 100,}}>DragonX NFTs!</h1> */}
            <h3 style={{ textAlign: "center" }}>
              <text className="dragonTitle">DragonX NFTs!</text><br />
              <div className="leftAndCenter">"Lorem ipsum dolor
                sit amet, consectetur
                adipiscing elit, sed do
                eiusmod tempor incididunt ut
                labore et dolore magna aliqua.
                Ut enim ad minim veniam, quis
                nostrud exercitation ullamco
                laboris nisi ut aliquip ex ea
                commodo consequat. Duis aute
                irure dolor in reprehenderit in
                voluptate velit esse cillum dolore
                eu fugiat nulla pariatur. Excepteur
                sint occaecat cupidatat non proident,
                sunt in culpa qui officia deserunt
                mollit anim id est laborum."</div>
              <br />
              <div style={{textAlign: "center", justifyContent: "center"}}>

              <text style={{display: "flex", flexDirection: "row"}}>
              <s.SpacerSmall/>
                <a href="https://rinkeby.etherscan.io/address/0xcC9399412501a3C0a8DB8CbF6639f15f96b6db86">
                  <img src={etherscan} style={{height: 50, width: 50}} />
                  </a>
                  <s.SpacerSmall/>
                <a href="https://discord.com">
                  <img src={discord} style={{height: 50, width: 70}} />
                  </a>  
                  <s.SpacerSmall/>
                <a href="https://twitter.com">
                <img src={twitter} style={{height: 50, width: 70}} />
                  </a>  
                  <s.SpacerSmall/>
                <a href="https://opensea.io">
                <img src={opensea} style={{height: 50, width: 50}} />
                  </a>
              </text>
              </div>

            </h3>
          </div>
          <div className="header-imgs">
            <div className="header-imgs-rows">
              <img src={i1} className="header-img" />
              <img src={i2} className="header-img" />
              <img src={i3} className="header-img" />
            </div>
            <div className="header-imgs-rows">
              <img src={i3} className="header-img" />
              <img src={gif} className="header-img" />
              <img src={i2} className="header-img" />
            </div>
            <div className="header-imgs-rows">
              <img src={i2} className="header-img" />
              <img src={i3} className="header-img" />
              <img src={i1} className="header-img" />
            </div>
          </div>
          
        </div>
        <text style={{color: "white", textAlign: "center", fontSize: 50, marginTop: 0}}>
          Total DragonX Minted: <br/>
          {blockchain.account == null ? "????" : (data.totalSupply)}/5000</text>
          <s.SpacerLarge/>
          <s.SpacerMedium/>
        <ResponsiveWrapper flex={1} style={{ padding: 24, paddingTop: 0 }}>
          <s.Container flex={1} jc={"center"} ai={"center"} style={{ paddingTop: 0 }}>
            <s.TextTitle
              style={{
                color: "white",
                textAlign: "center",
                maxWidth: 1000,
                fontSize: 30,
                paddingBottom: 0,
                fontWeight: "bold",
                borderStyle: "solid",
                borderColor: "black",
              }}>
            </s.TextTitle>
            <div>
              <s.Container
                className="mintButton"
                flex={1}
                jc={"center"}
                ai={"center"}
              >
                {Number(data.totalSupply) == 5000 ? (
                  <>
                    <s.TextTitle style={{ textAlign: "center" }}>
                      The sale has ended.
                    </s.TextTitle>
                    <s.TextDescription style={{ textAlign: "center" }}>
                      Dont worry, you're not missing out! You can still get Mutant Kaijuz on{" "}
                      <a
                        // target={"_blank"}
                        href={"https://opensea.io/"}
                      >
                        Opensea.io
                      </a>
                    </s.TextDescription>
                  </>
                ) : (
                  <>
                    <s.TextDescription style={{ textAlign: "center", fontSize: 30, color: "white" }}>
                      {feedback}
                    </s.TextDescription>
                    <s.SpacerMedium />
                    {blockchain.account === "" ||
                      blockchain.smartContract === null ? (
                      <s.Container ai={"center"} jc={"center"} style={{
                      }}>
                        <StyledButton
                          style={{
                            fontFamily: "coder",
                            borderWidth: 5,
                            borderStyle: "solid",
                            borderColor: "#FAEBD7",
                            fontSize: 80,
                            padding: 0,
                            paddingLeft: 100,
                            paddingRight: 100,
                            paddingBottom: 20,
                            fontFamily: 'Ceviche One',
                          }}
                          onClick={(e) => {
                            e.preventDefault();
                            dispatch(connect());
                            getData();
                          }}
                        >
                          {blockchain.errorMsg !== "" ? (
                            <>
                              <s.SpacerSmall />
                              <s.TextDescription style={{ textAlign: "center", fontSize: 20 }}>
                                <bold style={{ color: "white" }}>{blockchain.errorMsg}</bold>
                              </s.TextDescription>
                            </>
                          ) : null}
                          <s.SpacerSmall />
                          Mint Now!
                        </StyledButton>
                        <s.SpacerLarge />
                      </s.Container>
                    ) : (
                      <s.Container ai={"center"} jc={"center"} fd={"row"} style={{
                        marginTop: 0,
                        paddingTop: 0,
                        color: "white"
                      }}>
                        <form>
                          {/* I want  */}
                          <input
                            id="inputBox"
                            placeholder="#"
                            type="number"
                            min="1"
                            max="25"
                            style={{

                              fontSize: 30,
                              textAlign: "center",
                              backgroundColor: "#FAEBD7",
                              color: "black",
                              borderWidth: 4,
                              borderColor: "black",
                              borderStyle: "solid",
                              borderRadius: 100,
                              paddingRight: 10,
                              marginTop: 15,
                              // marginBottom: 20,
                              // paddingLeft: 0,
                              // marginLeft: 0,
                              width: 100,
                            }}
                          />
                          {/* Skulljunkies! */}
                        </form>
                        <s.SpacerSmall />
                        <StyledButton
                          style={{ fontFamily: 'Ceviche One', fontSize: 50, marginTop: 20, paddingRight: 40 }}
                          disabled={claimingNft ? 1 : 0}
                          onClick={(e) => {
                            e.preventDefault();
                            claimNFTs(1);
                            getData();
                          }}
                        >
                          {claimingNft ? "BUSY" : "MINT"}
                        </StyledButton>
                      </s.Container>
                    )}
                  </>
                )}
              </s.Container>
              <s.SpacerSmall />
            </div>
            <s.TextTitle
              style={{
                color: "white",
                textAlign: "center",
                maxWidth: 1000,
                fontSize: 30,
                paddingBottom: 0,
                fontWeight: "bold",
                borderStyle: "solid",
                borderColor: "black",
              }}>
            </s.TextTitle>
          </s.Container>
        </ResponsiveWrapper>
        <div className="infoPanel" style={{marginTop: 30}} >
          <div className="infoPanel-text" style={{paddingLeft: 100, paddingRight: 100}}>
            <h3 style={{textAlign: "center"}}>
            <div style={{textAlign: "center"}}><text className="dragonTitleTwo">What are DragonX NFTs?</text></div><br/>
            <text style={{textAlign: "center",}}>
              "Lorem ipsum dolor
              sit amet, consectetur
              adipiscing elit, sed do
              eiusmod tempor incididunt ut
              labore et dolore magna aliqua.
              Ut enim ad minim veniam, quis
              nostrud exercitation ullamco
              laboris nisi ut aliquip ex ea
              commodo consequat. Duis aute
              irure dolor in reprehenderit in
              voluptate velit esse cillum dolore
              eu fugiat nulla pariatur. Excepteur
              sint occaecat cupidatat non proident,
              sunt in culpa qui officia deserunt
              mollit anim id est laborum."</text>
              <s.SpacerLarge/>
              <s.SpacerLarge/>


              <div style={{textAlign: "center"}}><text className="dragonTitleTwo">Roadmap:</text></div>
            <text style={{textAlign: "center",}}>
              "Lorem ipsum dolor
              sit amet, consectetur
              adipiscing elit, sed do
              eiusmod tempor incididunt ut
              labore et dolore magna aliqua.
              Ut enim ad minim veniam, quis
              nostrud exercitation ullamco
              laboris nisi ut aliquip ex ea
              commodo consequat. Duis aute
              irure dolor in reprehenderit in
              voluptate velit esse cillum dolore
              eu fugiat nulla pariatur. Excepteur
              sint occaecat cupidatat non proident,
              sunt in culpa qui officia deserunt
              mollit anim id est laborum."</text> </h3>
          </div>
        </div>
        <s.Container jc={"center"} ai={"center"} style={{ width: "70%" }}>
          <s.TextDescription style={{ textAlign: "center", fontSize: 40 }}>
            <a
              href="https://rinkeby.etherscan.io/address/0xcC9399412501a3C0a8DB8CbF6639f15f96b6db86"
              style={{
                textDecoration: "none",
                color: "red",
                fontSize: 40,

              }}
            >DragonX NFTs Smart Contract </a> <text style={{ color: "white" }}></text>
          </s.TextDescription>
        </s.Container>
      </s.Container>
    </s.Screen>
  );
}

export default App;
